@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;background-color:#F7FAFB;font-family:'Montserrat', sans-serif}.main{height:85vh;overflow:hidden}.nav{display:flex;flex-direction:column;background-color:#062436;width:15%;float:left;height:inherit}.content{height:inherit}.container{width:100%}.container-header{height:75px;display:flex;width:80%;flex-direction:row}.container-header ul{display:flex;justify-content:flex-end;align-items:center;width:100%}.container-header ul li{justify-content:flex-end;margin-right:20px}.container-content{display:flex;max-height:80vh;padding-left:100px;padding-right:100px}.api{display:flex;background-color:#fff;overflow-x:scroll;max-height:inherit;padding:50px;flex-direction:column;width:100%}.footer{display:flex;height:15vh}.footer a{color:#36BDF7}.footer a:hover{color:#666}.footer ul{display:flex;flex-direction:column;justify-content:center}.footer{justify-content:space-between}.copyright{margin-left:100px}.contact{margin-right:100px}.logo{margin:4% 20% 20% 20%}nav ul{display:flex;flex-direction:column;align-items:center;justify-content:center}nav ul a{font-size:1.3em;margin-bottom:15px}nav ul li{margin-bottom:15px}ul{list-style:none;margin:0;padding-left:0}a{text-decoration:none}.container-header a{color:#666}.container-header a:hover{color:#36BDF7}nav ul a{color:#fff}.subtitle{font-size:1em;font-weight:500;margin-bottom:10px}.title{font-size:2em;font-weight:600}hr.rounded{border-top:2px #e6e6e6;border-radius:5px;width:100%}.content-nav ul{display:flex;flex-direction:row;justify-content:flex-start}.content-nav ul a{margin-right:20px}.content-nav ul a{color:#062436}.card{min-height:100px;display:flex;align-items:center}details{font-size:1rem;width:100%;background:#ffffff;position:relative}details .summary-title{-webkit-user-select:none;-ms-user-select:none;user-select:none}details:hover{cursor:pointer}details[open]{background-color:#F3FCFF}details[open] .summary-chevron-down{background:#F3FCFF}details .summary-content{border-top:1px solid #e2e8f0;cursor:default;padding:1em;font-weight:300;line-height:1.5}details summary{padding:1em}details summary:focus{outline:none}details summary:hover .summary-chevron-up svg{opacity:1}details .summary-chevron-up svg{opacity:0.5}details .summary-chevron-up,details .summary-chevron-down{pointer-events:none;position:absolute;top:0.75em;right:1em;background:#fff}details .summary-chevron-up svg,details .summary-chevron-down svg{display:block}details summary::-webkit-details-marker{display:none}

